const mutations = {
    /**
     * user
     */
    setUser: (state, payload) =>{
        state.isLogin = true;
        state.user = payload;
    },
    /**
     * 
     */
    signOut: (state) => {
        state.user = {};
        sessionStorage.clear();
        state.isLogin = false;
    }
}

export default mutations
