<template>
  <div id="mountNode" :style="{width:width}"
       v-loading="testLoading"
       element-loading-text="">
    <div class="editor" >
      <!---->
      <context-menu />
      <!-- toolbar -->
      <toolbar @save="handleSave" :is-edit="isEdit" @test="handleTest"/>
      <!-- div -->
      <div style="height: 42px;"></div>
      <!-- g6 -->
      <div class="bottom-container">
        <!--  -->
        <item-panel />
        <!--  -->
        <detail-panel />
        <!--  -->
        <minimap />
        <!-- g6 -->
        <page :height="height" :width="width"  :data="data" />
      </div>
    </div>
    <Flow />
    <el-dialog
            title=""
            :visible.sync="saveDialogVisible"
            width="30%"
            @close="onSaveDialogClose">
      <el-form
              ref="saveForm"
              :model="saveForm"
              label-width="120px"
              :rules="saveRules">
        <el-form-item label="" prop="type">
          <el-select
                  v-model="saveForm.type"
                  filterable
                  allow-create
                  placeholder="">
            <el-option
                    v-for="item in typeOption"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="saveForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="canUse">
          <el-checkbox v-model="saveForm.canUse" :true-label="1" :false-label="0"></el-checkbox>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="onSaveDialogClose"> </el-button>
        <el-button type="primary" @click="onSaveAsDraft"></el-button>
        <el-button type="primary" @click="onSave"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import Toolbar from "../Toolbar";
  import ItemPanel from "../ItemPanel";
  import DetailPanel from "../DetailPanel";
  import Minimap from "../Minimap";
  import Page from "../Page";
  import Flow from "../Flow"
  import ContextMenu from "../ContextMenu";
  import Editor from "../../components/Base/Editor";
  import command from "../../command";
  import {editorAPI} from "../../../../globalAPI";
  import {deepClone, randomn} from '../../../../utils/index';
  import {savaAs} from 'file-saver';
  import {mapGetters} from "vuex";

  export default {
  name: "G6Editor",
  components: {
    Toolbar,
    ItemPanel,
    DetailPanel,
    Minimap,
    Page,
    ContextMenu,
    Flow
  },
  computed: {
    ...mapGetters({
              user: "user"
        }
    )
  },
  props: {
    height: {
      type: Number,
      default: document.documentElement.clientHeight
    },
    width: {
      type: Number,
      default: document.documentElement.clientWidth
    },
    //  graph data
    data: {
      type: Object,
      default: () => {}
    },
    //  graph name
    name: {
      type: String,
      default: ""
    },
    //  graph id
    id: {
      type: Number,
      default: -1
    },
    //  editor
    isEdit: {
      type: Boolean,
      default: false
    },
    status: {
      type: String,
      default: "normal"
    }
  },
  created() {
    this.init();
  },
  data() {
    return {
      editor: {},
      command: null,
      // data
      graphData: {},
      // dialog
      saveDialogVisible: false,
      typeOption: [
        {label: "", value: 0},
        {label: "", value: 1}
      ],
      saveForm: {
        type: 0,
        name: '',
        canUse: 0
      },
      saveRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
        ],
        type: [
          { required: true, message: '', trigger: 'change' }
        ]
      },
      testLoading: false
    };
  },
  methods: {
    /**
     * editor
     */
    init() {
      this.editor = new Editor();
      this.command = new command(this.editor);
    },
    /**
     *  
     * @param data  graphData
     */
    checkPoint(data) {
        this.graphData = data;
        let isCheck = true;
        // 
        for (let i in this.graphData.nodes) {
            // 
            this.editor.graph.findById(this.graphData.nodes[i].id).clearStates("error");
            //  attr 
            if (this.graphData.nodes[i].attr) {
                //  bindForm 
                if (this.graphData.nodes[i].attr.bindForm !== undefined) {
                    let attr = this.graphData.nodes[i].attrName.find(value => {
                      return value.attribute === "bindForm"
                    });
                    if(attr.required) {
                      //  bindForm 
                      if (this.graphData.nodes[i].attr.bindForm.length === 0) {
                        //  error
                        let errorNode = this.editor.graph.findById(this.graphData.nodes[i].id);
                        errorNode.setState("error", true);
                        isCheck = false;
                      }
                    }
                }
                //  inChargeGroupID 
                if (this.graphData.nodes[i].attr.inChargeGroupID !== undefined) {
                  let attr = this.graphData.nodes[i].attrName.find(value => {
                    return value.attribute === "inChargeGroupID"
                  });
                  if(attr.required) {
                    //  inChargeGroupID 
                    if (this.graphData.nodes[i].attr.inChargeGroupID.length === 0) {
                      //  error
                      let errorNode = this.editor.graph.findById(this.graphData.nodes[i].id);
                      errorNode.setState("error", true);
                      isCheck = false;
                    }
                  }
                }
                //  roomGroups 
                if (this.graphData.nodes[i].attr.roomGroups !== undefined) {
                  let attr = this.graphData.nodes[i].attrName.find(value => {
                    return value.attribute === "roomGroups"
                  });
                  if(attr.required) {
                    //  roomGroups 
                    if (this.graphData.nodes[i].attr.roomGroups.length === 0) {
                      //  error
                      let errorNode = this.editor.graph.findById(this.graphData.nodes[i].id);
                      errorNode.setState("error", true);
                      isCheck = false;
                    }
                  }
                }
            }
        }
        //  
        this.editor.graph.paint();
        return isCheck;
    },
    /**
     * toolbar
     * @param data 
     * @param type saveAs save
     */
    handleSave(data, type) {
      let isCheck = this.checkPoint(data);
      if (isCheck) {
        //  
        this.handleShowSaveDialog(type);
      } else {
        this.graphData = {};
        this.$notify.error({
          title: '',
          message: ''
        });
      }
    },
    /**
     * 
     */
    async getRunDto(data) {
      let graphData = data;
      //  params
      let startID, endID, cycleID;
      //
      for (let i in graphData.nodes) {
        switch (graphData.nodes[i].mType) {
          case "startNode":
            startID = graphData.nodes[i].id;
            break;
          case "endNode":
            endID = graphData.nodes[i].id;
            break;
          case "cycleNode":
            cycleID = graphData.nodes[i].id;
            break;
        }
      }
      //   
      graphData = await this.handleExpandEmbedNode(graphData);
      let nodes = [];
      //   nodesajax
      for (let i in graphData.nodes) {
        let nextID;
        if (graphData.nodes[i].mType === "cycleNode") {
          nextID = this.findNextNodes(this.findPreviousNodes(graphData.nodes[i].id, graphData), graphData, true);
        } else {
          nextID = this.findNextNodes(graphData.nodes[i].id, graphData);
        }
        let item = {
          nodeID: graphData.nodes[i].id
        };
        // 
        if (nextID.length > 0) {
          item.nextNodeID = nextID;
        }
        //  mType  nodeKind  
        switch (graphData.nodes[i].mType) {
          case "startNode":
            item.kind = 1;
            break;
          case "endNode":
            item.beginNodeID = startID;
            item.kind = 2;
            break;
          case "cycleNode":
            item.kind = 3;
            item.loopBeginNodeID = graphData.nodes[i].loopBeginNodeID;
            break;
          case "wfdNode":
            item.kind = 0;
            break;
          case "syncNode":
            // 
            if (graphData.nodes[i].syncEndID) {
              item.kind = 1;
            } else {
              //
              item.kind = 2;
              item.beginNodeID = graphData.nodes[i].syncBeginID;
            }
            break;
          case "concurrencyNode":
            // 
            if (graphData.nodes[i].concurrencyEndID) {
              item.kind = 1;
            } else {
              // 
              item.kind = 2;
              item.beginNodeID = graphData.nodes[i].concurrencyBeginID;
            }
            break;
        }
        if (graphData.nodes[i].attr) {
          //  loopBeginNodeID
          if (graphData.nodes[i].attr.loopBeginNodeID) {
            item.loopBeginNodeID = graphData.nodes[i].attr.loopBeginNodeID;
          }
        }
        nodes.push(item);
      }
      return {
        beginID: startID,
        endID: endID,
        nodes: nodes
      };
    },
    /**
     *   
     * @param data  graphData
     * @return  graphData
     */
    async handleExpandEmbedNode(data) {
      // 
      if(data.nodes.find(value => {
        return value.mType === "embedNode";
      })) {
        // 
        let graphData = deepClone(data);
        //  
        let processData = {};
        //  
        for(let item of graphData.nodes) {
          if(item.mType === "embedNode") {
            // item: 
            let response = await this.getProcessContent(item.attr.processID);
            if(response.data.resultCode === 1){
              processData = JSON.parse(response.data.objData.content);
              // 
              let inEmbedNodeEdge, outEmbedNodeEdge;
              for (let edge of graphData.edges) {
                if (edge.targetId === item.id) {
                  inEmbedNodeEdge = edge;
                } else if(edge.sourceId === item.id) {
                  outEmbedNodeEdge = edge;
                }
              }
              //  index
              let embedIndex = graphData.nodes.findIndex(value => {
                return value.id === item.id;
              })
              // 
              graphData.nodes.splice(embedIndex, 1);
              //  
              let embedProcessStartNode, embedProcessEndNode;
              for(let node of processData.nodes) {
                if (node.mType === "startNode") {
                  embedProcessStartNode = node;
                } else if(node.mType === "endNode") {
                  embedProcessEndNode = node;
                }
              }
              //  
              let embedProcessStartEdge, embedProcessEndEdge;
              for (let edge of processData.edges) {
                if (edge.sourceId === embedProcessStartNode.id) {
                  embedProcessStartEdge = edge; // 1
                } else if(edge.targetId === embedProcessEndNode.id) {
                  embedProcessEndEdge = edge; // 2
                }
              }
              // 
              graphData.edges.forEach((value, index, arr) => {
                if (value.id === inEmbedNodeEdge.id) {
                  graphData.edges[index].targetId = embedProcessStartEdge.targetId;
                  graphData.edges[index].target = embedProcessStartEdge.target
                } else if (value.id === outEmbedNodeEdge.id) {
                  graphData.edges[index].sourceId = embedProcessEndEdge.sourceId;
                  graphData.edges[index].source = embedProcessEndEdge.source
                }
              })
              //   
              let index;
              // 
              index = processData.nodes.findIndex(value => {
                return value.id === embedProcessStartNode.id;
              })
              // 
              processData.nodes.splice(index, 1);
              // 
              index = processData.nodes.findIndex(value => {
                return value.id === embedProcessEndNode.id;
              })
              // 
              processData.nodes.splice(index, 1);
              // 
              index = processData.edges.findIndex(value => {
                return value.id === embedProcessStartEdge.id;
              })
              // 
              processData.edges.splice(index, 1);
              // 
              index = processData.edges.findIndex(value => {
                return value.id === embedProcessEndEdge.id;
              })
              // 
              processData.edges.splice(index, 1);
              //    
              graphData.nodes = [...graphData.nodes, ...processData.nodes];
              graphData.edges = [...graphData.edges, ...processData.edges];
              //  
              graphData = this.handleExpandEmbedNode(graphData);
              return graphData;
            }
          }
        }
      } else {
        return data;
      }
    },
    /**
     *  ajax 
     * @param data  graphData
     */
    async handleTest(data) {
      let isCheck = this.checkPoint(data);
      if (isCheck) {
          this.testLoading = true;
          //  runDto  ajax 
          let params = await this.getRunDto(data);
          // ajax  
          this.axios.post(editorAPI.runTest, this.qs.parse(params))
                  .then((response) => {
                    if(response.data.resultCode === 1){
                      this.testLoading = false;
                      this.$notify({
                        title: '',
                        message: response.data.objData,
                        type: 'success'
                      });
                    }
                  })
                  .catch((error) => {
                    this.testLoading = false;
                    if(error.response.data.resultCode === 3001) {
                      for(let nodeID of error.response.data.objData.nodeID) {
                        // 
                        let errorNode = this.editor.graph.findById(nodeID);
                        // 
                        errorNode.clearStates("error");
                        // error
                        errorNode.setState("error", true);
                      }
                      this.editor.graph.paint();
                      let errorMessage = error.response.data.objData.errorDetail.split("")[0]
                              + "";
                      this.$notify.error({
                        title: error.response.data.message,
                        message: errorMessage,
                        duration: 0
                      });
                    }
                  });
      } else {
          this.graphData = {};
          this.$notify.error({
              title: '',
              message: ''
          });
      }
    },
    /**
     * ajax  
     * @param id ID
     * @return Promise 
     */
    getProcessContent(id) {
      return this.axios.post(editorAPI.downloadOneProcess, this.qs.stringify({
        processId: id,
        userId: this.user.id
      }));
    },
    /**
     * 
     * @param id id
     * @param graphData 
     * @param flag 
     * @returns {Array} 
     */
    findNextNodes(id, graphData, flag = false) {
        let arr = [];
        for (let i in graphData.edges) {
            if (graphData.edges[i].sourceId === id) {
                let targetNode = graphData.nodes.find(value => {
                    return value.id === graphData.edges[i].targetId
                });
                //  flag   previousNode 
                //   push 
                //  nextID 
                if (flag && targetNode.mType !== "cycleNode") {
                    // 
                    arr.push(graphData.edges[i].targetId);
                }
                //  flag  
                if(!flag) {
                    //  
                    if(targetNode.mType === "cycleNode") {
                        return Array.of(graphData.edges[i].targetId);
                    } else {
                        // 
                        arr.push(graphData.edges[i].targetId);
                    }
                }
            }
        }
        return arr;
    },
    /**
     * 
     * @param id id
     * @param graphData 
     * @returns sourceId 
     */
    findPreviousNodes(id, graphData) {
        for (let i in graphData.edges) {
            if (graphData.edges[i].targetId === id) {
                // 
                return graphData.edges[i].sourceId;
            }
        }
    },
    /**
     * saveDialog 
     */
    onSaveDialogClose() {
      this.resetSaveForm();
      this.saveDialogVisible = false;
    },
    /**
     * 
     * @param type 'saveAs' 'save'
     */
    handleShowSaveDialog(type) {
      switch (type) {
        case "saveAs":
          //  saveDialog
          this.saveDialogVisible = true;
          break;
        case "save":
          if(this.isEdit) {
            //  
            if(this.status === "normal") {
              this.handleUpdateFlow();
            } else if (this.status === "draft") {
              this.handleUpdateDraft();
            }
          } else {
            //  
            this.saveDialogVisible = true;
          }
          break;
      }
    },
    /**
     * ajax 
     */
    handleUpdateFlow() {
      //  param
      let param = new FormData();
      param.append("processName", this.name);
      let blob = new Blob([JSON.stringify(this.graphData)], {type: "text/plain;charset=utf-8"});
      param.append("processFile", new File([blob], "flow" + randomn(10) + ".txt"));
      param.append("oldProcessId", this.id);
      param.append("runDtoStr", JSON.stringify(this.getRunDto(this.graphData)));
      param.append("canUse", this.saveForm.canUse);
      param.append("type", this.saveForm.type);
      this.axios.post(editorAPI.updateProcess, param)
              .then((response) => {
                if(response.data.resultCode === 1){
                  this.$message({
                      type: "success",
                      message: response.data.message
                  });

                }
              })
              .catch((error) => {
                  this.$message({
                      type: "error",
                      message: error.response.data.message
                    });
              });
    },
    /**
     * ajax 
     */
    handleUpdateDraft() {
      //  param
      let param = new FormData();
      param.append("processName", this.name);
      let blob = new Blob([JSON.stringify(this.graphData)], {type: "text/plain;charset=utf-8"});
      param.append("processFile", new File([blob], "draftFlow" + randomn(10) + ".txt"));
      param.append("oldProcessId", this.id);
      this.axios.put(editorAPI.updateDraft, param)
              .then((response) => {
                if(response.data.resultCode === 1){
                  this.$message({
                    type: "success",
                    message: response.data.message
                  });

                }
              })
              .catch((error) => {
                this.$message({
                  type: "error",
                  message: error.response.data.message
                });
              });
    },
    /**
     * 
     */
    onSave() {
      this.$refs.saveForm.validate((valid) => {
        if(valid) {
          let param = new FormData();
          param.append("processName", this.saveForm.name);
          let blob = new Blob([JSON.stringify(this.graphData)], {type: "text/plain;charset=utf-8"});
          param.append("processFile", new File([blob], "flow" + randomn(10) + ".txt"));
          param.append("runDtoStr", JSON.stringify(this.getRunDto(this.graphData)));
          param.append("canUse", this.saveForm.canUse);
          param.append("type", this.saveForm.type);
          // ajax
          this.axios.post(editorAPI.uploadProcess, param)
                  .then((response) => {
                    // 
                    if(response.data.resultCode === 1){
                      this.$message({
                        message: response.data.message,
                        type: 'success'
                      });
                      this.resetSaveForm();
                      this.saveDialogVisible = false;
                    } else{
                      // 
                      this.$message({
                        message: response.data.message,
                      });
                    }
                  })
                  .catch((error) => {
                    this.$message({
                      type: 'error',
                      message: error.response.data.message
                    })
                  });
        } else{
          return false;
        }
      });
    },
    /**
     * 
     */
    onSaveAsDraft() {
      this.$refs.saveForm.validate((valid) => {
        if(valid) {
          let param = new FormData();
          param.append("name", this.saveForm.name);
          let blob = new Blob([JSON.stringify(this.graphData)], {type: "text/plain;charset=utf-8"});
          param.append("processFile", new File([blob], "draftFlow" + randomn(10) + ".txt"));
          // ajax
          this.axios.post(editorAPI.saveDraft, param)
                  .then((response) => {
                    // 
                    if(response.data.resultCode === 1){
                      this.$message({
                        message: response.data.message,
                        type: 'success'
                      });
                      this.resetSaveForm();
                      this.saveDialogVisible = false;
                    } else{
                      // 
                      this.$message({
                        message: response.data.message,
                      });
                    }
                  })
                  .catch((error) => {
                    this.$message({
                      type: 'error',
                      message: error.response.data.message
                    })
                  });
        } else{
          return false;
        }
      });
    },
    resetSaveForm() {
      this.$refs.saveForm.resetFields();
      this.saveForm = {
        type: 0,
        name: '',
        canUse: 0
      }
    }
  }
};
</script>

<style scoped>
.editor {
  position: relative;
  width: 100%;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.bottom-container {
  position: relative;
}
</style>
