export default (_self, h) => {
    return [
        h("el-select", {
                props: {
                    value: _self.obj.value || "",
                },
                attrs: {
                    placeholder: _self.obj.attr.placeholder || "",
                    filterable: true
                },
                on: {
                    'change'(arr) {
                        _self.obj.value = arr;
                        _self.$emit('handleChangeVal', arr)
                    }
                }
            },
            _self.obj.attr.items.map(v => {
                return h("el-option", {
                    props: {
                        label: v.label_name,
                        value: v.label_value
                    }
                })
            }))
    ];
};

