<template>
  <div style="margin: 20px 10px;">
      <link
          rel="stylesheet"
          type="text/css"
          href="//at.alicdn.com/t/font_1581731_z26piaggta.css"
      />
      <div style="margin-left: 35px;">
          <el-button @click="handleBack" style="margin-right: 15px;" icon="el-icon-back" type="info" plain v-if="isEdit"></el-button>
          <el-button @click="handleShowSaveDialog('saveAs')" icon="iconfont icon-save-as" type="primary" plain> </el-button>
          <el-button @click="handleShowSaveDialog('save')" style="margin-left: 15px;" icon="iconfont icon-save" type="success" plain> </el-button>
      </div>
      <el-row class="formContainer">
          <el-col :span="7" class="formList">
              <!--form-->
              <el-form ref="form" class="b-a" label-width="100px" style="overflow-y: scroll">
                  <h2 style="text-align: center"></h2>
                  <el-collapse style="padding: 15px;">
                      <el-collapse-item title="" name="1">
                          <!--listlistv-for-->
                          <!--clone-->
                          <!--options-->
                          <draggable :clone="cloneData" :list="commonList" :options="dragOptionsLeft">
                              <transition-group class="form-list-group" type="transition" :name="'flip-list'" tag="div">
                                  <renders
                                          v-for="(items, index) in commonList"
                                          :key="index"
                                          :ele="items.ele"
                                          :obj="items.obj">
                                  </renders>
                              </transition-group>
                          </draggable>
                      </el-collapse-item>
                      <!--<el-collapse-item title="" name="2">-->
                          <!--<draggable :clone="cloneData" :list="formBaseList" :options="dragOptionsLeft">-->
                              <!--<transition-group class="form-list-group" type="transition" :name="'flip-list'" tag="div">-->
                                  <!--<renders-->
                                          <!--v-for="(items, index) in formBaseList"-->
                                          <!--:key="index"-->
                                          <!--:ele="items.ele"-->
                                          <!--:obj="items.obj">-->
                                  <!--</renders>-->
                              <!--</transition-group>-->
                          <!--</draggable>-->
                      <!--</el-collapse-item>-->
                  </el-collapse>
              </el-form>
          </el-col>
          <el-col :span="17" class="formList">
              <!--form-->
              <el-form ref="form" :model="formData" class="b-a" label-width="120px" style="overflow-y: scroll">
                  <h2 style="text-align: center"></h2>
                  <draggable
                          :clone="cloneData"
                          :list="dataList"
                          :options="dragOptionsRight">
                      <transition-group class="form-list-group-right" type="transition" :name="!drag ? 'flip-list' : null" tag="div">
                          <renders
                                  v-for="(items, index) in dataList"
                                  :key="index"
                                  :ele="items.ele"
                                  :obj="items.obj"
                                  :index="index"
                                  :config-icon="true"
                                  @handleChangeVal="val => handleChangeVal(val,items)"
                                  @handleConfigElement="handleConfig"
                                  @handleRemoveElement="handleRemoveElement">
                          </renders>
                      </transition-group>
                  </draggable>
              </el-form>
          </el-col>
      </el-row>
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        :close-on-click-modal="false"
        width="40%"
        :before-close="handleCancel">
        <el-form :model="attrForm" size="small" ref="attrForm">
            <!-- attr -->
            <el-form-item
                    v-for="(item) in attrsList"
                    :key="item.name"
                    :label="item.name"
                    :prop="item.attribute"
                    :rules="{
                        required: true, message: item.name + '', trigger: 'blur'
                    }"
                    label-width="100px">
                <el-input
                    v-model="attrForm[item.attribute]"
                    v-if="item.type === 'input'"
                    :disabled="item.clientType !== 'sell'">
                </el-input>
                <el-input-number
                    v-model="attrForm[item.attribute]"
                    v-if="item.type === 'inputNumber'"
                    :disabled="item.clientType !== 'sell'"
                    :precision="0">
                </el-input-number>
                <!--TODO: select-->
                <!--<el-select v-model="value" placeholder="" v-if="item.type === 'select'" :disabled="item.clientType !== 'sell'">-->
                    <!--<el-option-->
                        <!--v-for="item in options"-->
                        <!--:key="item.value"-->
                        <!--:label="item.label"-->
                        <!--:value="item.value">-->
                    <!--</el-option>-->
                <!--</el-select>-->
                <el-checkbox v-model="attrForm[item.attribute]" v-if="item.type === 'singleCheck'" :disabled="item.clientType !== 'sell'"></el-checkbox>
            </el-form-item>
            <!--  -->
            <div v-if="attrForm.items">
                <el-divider></el-divider>

                        <el-form-item
                                v-for="(item, index) in attrForm.items"
                                :label="'' + (index + 1)"
                                :key="item.value"
                                :prop="'items.' + index + '.label_name'"
                                :rules="{
                                required: true, message: '', trigger: 'blur'
                            }"
                                label-width="100px">
                            <el-row>
                                <el-col :span="21">
                            <el-input v-model="item.label_name" placeholder=""></el-input>
                                </el-col>
                            <el-col :span="3">
                                <el-button @click.prevent="removeOption(item)" size="small"></el-button>
                            </el-col>
                            </el-row>
                        </el-form-item>
                <el-form-item>
                    <el-button @click="addOption" style="margin-left: 100px"></el-button>
                </el-form-item>
            </div>
        </el-form>
        <span slot="footer">
            <el-button @click="handleCancel"> </el-button>
            <el-button type="primary" @click="handleOk"> </el-button>
        </span>
      </el-dialog>
      <el-dialog
              title=""
              :visible.sync="saveDialogVisible"
              width="30%">
          <el-form
                  ref="saveForm"
                  :model="saveForm"
                  label-width="80px"
                  :rules="saveRules">
              <el-form-item label="" prop="name">
                  <el-input v-model="saveForm.name" placeholder=""></el-input>
              </el-form-item>
          </el-form>
          <span slot="footer">
        <el-button @click="onSaveDialogClose"> </el-button>
        <el-button type="primary" @click="handleSubmit"> </el-button>
      </span>
      </el-dialog>
  </div>
</template>
<script>
// @ is an alias to /src
import draggable from "vuedraggable";
import { commonList, formBaseList } from "../components/custom/FormList";
import renders from "../components/custom/Render";
import { formAPI } from "../../../globalAPI";
import { randomn, genID,deepClone } from '../../../utils/index';
import { mapGetters } from "vuex";
export default {
  name: "home",
  data() {
    return {
      commonList,
      formBaseList,
      // 
      dataList: [],
      // 
      formData: {},
      // 
      formName: "",
      // ID
      formID: "",
      // 
      isEdit: false,
      drag: false,
      //  dialog 
      dialogVisible: false,
      //  form 
      attrForm: {},
      //  form 
      attrsList: [],
      //  index
      editIndex: 0,
      //  dialog 
      saveDialogVisible: false,
      //  form 
      saveForm: {
          type: '',
          name: ''
      },
      //  form 
      saveRules: {
          name: [
              { required: true, message: '', trigger: 'blur' },
          ],
          // type: [
          //   { required: true, message: '', trigger: 'change' }
          // ]
      }
    };
  },
  components: {
    draggable,
    renders
  },
  methods: {
    initData() {
        //  formId 
        if (this.$route.params.formId) {
            this.formID = this.$route.params.formId;
            this.isEdit = true;
            this.axios
                .post(
                    formAPI.downloadOneForm,
                    this.qs.stringify({
                        formId: this.$route.params.formId,
                        userId: this.user.id
                    })
                )
                .then(response => {
                    if (response.data.resultCode === 1) {
                        this.dataList = JSON.parse(response.data.objData.content);
                        this.formName = response.data.objData.name;
                    }
                })
                .catch(error => {
                    this.$message({
                        type: "error",
                        message: error.response.data.message
                    });
                });
        }
    },
    handleBack() {
      // 
      this.$router.go(-1);
    },
    handleShowSaveDialog(type) {
      switch (type) {
          // 
          case "saveAs":
              //  saveDialog
              this.saveDialogVisible = true;
              break;
          case "save":
              if(this.isEdit) {
                  //  
                  this.handleUpdateForm();
              } else {
                  //  
                  this.saveDialogVisible = true;
              }
              break;
      }
    },
    handleUpdateForm() {
        for(let i in this.dataList) {
            // List value
            this.dataList[i].obj.value = "";
            this.dataList[i].obj.id = genID(2);
        }
        //  param
        let param = new FormData();
        param.append("formName", this.formName);
        let blob = new Blob([JSON.stringify(this.dataList)], {type: "text/plain;charset=utf-8"});
        param.append("formFile", new File([blob], "form" + randomn(10) + ".txt"));
        param.append("oldFormId", this.formID);
        this.axios.post(formAPI.updateForm, param)
                .then((response) => {
                  if(response.data.resultCode === 1){
                    this.$message({
                        type: "success",
                        message: response.data.message
                    });

                  }
                })
                .catch((error) => {
                    this.$message({
                        type: "error",
                        message: error.response.data.message
                      });
                });
    },
    /**
     *  saveDialog  methods
     */
    handleSubmit() {
      this.$refs.saveForm.validate((valid) => {
          if (valid) {
              for(let i in this.dataList) {
                  // List value
                  this.dataList[i].obj.value = "";
                  this.dataList[i].obj.id = genID(2);
              }
              console.log(this.dataList);
              console.log(JSON.stringify(this.dataList));
              //  param
              let param = new FormData();
              param.append("formName", this.saveForm.name);
              let blob = new Blob([JSON.stringify(this.dataList)], {type: "text/plain;charset=utf-8"});
              param.append("formFile", new File([blob], "form" + randomn(10) + ".txt"));
              // ajax
              this.axios.post(formAPI.uploadForm, param)
                  .then((response) => {
                      // 
                      if(response.data.resultCode === 1){
                          this.$message({
                              message: response.data.message,
                              type: 'success'
                          });
                          this.resetSaveForm();
                          this.saveDialogVisible = false;
                      } else{
                          // 
                          this.$message({
                              message: response.data.message,
                          });
                      }
                  })
                  .catch((error) => {
                      this.$message({
                          type: 'error',
                          message: error.response.data.message
                      })
                  });
          }
      })
    },
    // https://github.com/SortableJS/Vue.Draggable#clone
    // ,
    cloneData(original) {
      // 
      return JSON.parse(JSON.stringify(original));
    },
    // 
    handleChangeVal(val, element) {
      this.$set(this.formData, element.obj.name, val);
    },
    /**
     * icon
     *  
     */
    handleConfig(index) {
      this.editIndex = index;
      this.attrsList = this.dataList[index].obj.attrName;
      //  attr   attrForm
      const temp = Object.assign({}, this.dataList[index].obj);
      this.attrForm = deepClone(temp.attr);
      // Vue   Vue 
      this.attrForm = Object.assign({}, this.attrForm);
      this.dialogVisible = true
    },
    /**
     * icon
     */
    handleRemoveElement(index) {
      //  dataList 
      this.dataList.splice(index, 1);
    },
    /**
     *  saveDialog 
     */
    onSaveDialogClose() {
        this.resetSaveForm();
        this.saveDialogVisible = false;
    },
    /**
     *  saveForm 
     */
    resetSaveForm() {
        this.$refs.saveForm.resetFields();
        this.saveForm = {
            type: '',
            name: ''
        }
    },
    /**
     *  saveDialog 
     */
    handleOk() {
        //  attrForm  dataList  obj.attr
        //  Vue   render 
        //  assign  Vue   
        if(this.attrForm.items) {
            this.$refs["attrForm"].validate((valid) => {
                if(valid) {
                    // this.dataList[this.editIndex].obj.attr = Object.assign({}, this.dataList[this.editIndex].obj.attr, this.attrForm);
                    this.dataList[this.editIndex].obj.attr = deepClone(this.attrForm);
                    this.handleCancel();
                }
            })
        } else {
            // this.dataList[this.editIndex].obj.attr = Object.assign({}, this.dataList[this.editIndex].obj.attr, this.attrForm);
            this.dataList[this.editIndex].obj.attr = deepClone(this.attrForm);
            this.handleCancel();
        }
    },
    /**
     *  saveDialog 
     */
    handleCancel() {
        // dialog
        this.dialogVisible = false;
        setTimeout(_ => {
            //  attrForm
            this.attrForm = {};
            // 
            this.$refs["attrForm"].resetFields();
        }, 500)
    },
    /**
     *  Dialog 
     * @param item 
     */
    removeOption(item) {
        let index = this.attrForm.items.indexOf(item);
        if (index !== -1) {
            this.attrForm.items.splice(index, 1)
        }
    },
    addOption() {
        let length = this.attrForm.items.length;
        this.attrForm.items.push({
            label_name: "",
            label_value: length + 1
        })
    },
  },
  computed: {
    // 1
    dragOptionsLeft() {
      return {
        //  ms
        animation: 50,
        // 
        // class
        // 
        ghostClass: "ghost",
        // 
        // name
        group: {
          name: "shared",
          pull: "clone",
          revertClone: false
        },
        // 
        sort: false
      };
    },
    // 2
    dragOptionsRight() {
      return {
        animation: 50,
        ghostClass: "ghost",
        group: {
          // shared,pull
          put: ["shared"]
        },
      };
    },
      ...mapGetters({
          user: "user"
      })
  },
  created() {
    this.initData();
  }
};

</script>
<style>
.formContainer {
  padding: 20px;
}
.form-list-group-right {
  min-height: 60vh;
  padding: 20px !important;
}

.b-a {
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 80vh;
}
.formList {
  padding-left: 15px;
  padding-right: 15px;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
.item-icon {
  transition: all 1.5s ease;
  position: absolute;
  top: -30px;
  right: 0px;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
}
.item-icon i {
  cursor: pointer !important;
  margin-right: 5px;
}

.items:hover .item-icon {
  transition: inherit;
  opacity: 1;
  max-height: 50px;
}
.form-list-group {
  padding: 20px !important;
}
</style>
