<!-- TODO -->
<template>
  <ul>
    <li
      v-for="(item,index) in list"
      :key="index"
      class="getItem"
      :data-shape="item.shape"
      :data-type="item.type"
      :data-size="item.size"
      draggable
      @dragstart="handleDragstart($event, item)"
      @dragend="handleDragEnd($event,item)">
      <span class="pannel-type-icon" :style="{background:'url('+item.image+')'}"></span>
      {{item.name}}
    </li>
  </ul>
</template>

<script>
import eventBus from "../../utils/eventBus";
import {nodeList} from "../../nodeList"
import { deepClone } from "@/utils";
export default {
  data() {
    return {
      // g6
      page: null,
      // command
      command: null,
      // X/Y
      offsetX: 0,
      offsetY: 0,
      // 
      list: nodeList
    };
  },
  created() {
    this.bindEvent();
  },
  methods: {
    /**
     * 
     * @param e event
     */
    handleDragstart(e) {
      // X/Y
      this.offsetX = e.offsetX;
      this.offsetY = e.offsetY;
    },
    /**
     * 
     * @param e event
     */
    handleDragEnd(e, item) {
      let mItem = item;
      // 
      if(item.mType === 'startNode' || item.mType === 'endNode' || item.mType === 'cycleNode') {
        let nodes = this.page.graph.save().nodes;
        for(let i in nodes){
            //  return 
          if(nodes[i].mType === item.mType) {
            this.$notify.error({
              title: '',
              message: item.name + ' '
            });
            return;
          }
        }
      }
      if (this.page) {
        //  page  graph 
        const graph = this.page.graph;
        //  
        if(item.mType === "syncNode") {
            let startPoint = {};
            let endPoint = {};
            let startID, endID;
            //  
            //  item  data  
            startPoint = deepClone(mItem);
            //  data X/Y
            startPoint.offsetX = this.offsetX;
            startPoint.offsetY = this.offsetY;
            //  X/Y 
            const xy = graph.getPointByClient(e.x, e.y);
            startPoint.x = xy.x;
            startPoint.y = xy.y;
            //  data  size 
            startPoint.size = [];
            startPoint.size = mItem.size.split("*");
            //  data  type  node
            startPoint.type = "node";
            //  data  label
            startPoint.name = "";
            startPoint.label = "";
            //  endPoint   
            endPoint = deepClone(startPoint);
            // 
            startPoint.nodeKind =  1;
            //  
            // TODO:  nodeList 
            startPoint.attr = {
                bindForm: '',
                time: 0,
                inChargeGroupID: '',
                inChargeUserID: '',
                roomGroups: [],
                needDevice: false,
                needAuditing: false
            };
            // TODO:  nodeList 
            startPoint.attrName = [
                {
                    name: '',
                    attribute: 'bindForm',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '',
                    attribute: 'inChargeGroupID',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '',
                    attribute: 'inChargeUserID',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '',
                    attribute: 'roomGroups',
                    type: 'multiSelect',
                    clientType: 'sell'
                },
                {
                    name: '',
                    attribute: 'time',
                    type: 'inputNumber',
                    min: 0,
                    clientType: 'sell'
                },
                {
                    name: '',
                    attribute: 'needDevice',
                    type: 'singleCheck',
                    required: true,
                    clientType: 'sell'
                },
                {
                    name: '',
                    attribute: 'needAuditing',
                    type: 'singleCheck',
                    required: true,
                    clientType: 'sell'
                }
            ];
            //  data  command 
            // ID
            startID = this.command.executeCommand("add", [startPoint]);
            //  
            endPoint.offsetY += 100;
            endPoint.y += 100;
            //  endPoint  label
            endPoint.name = "";
            endPoint.label = "";
            //  endPoint  ID
            endPoint.syncBeginID = startID;
            // 
            endPoint.nodeKind =  2;
            //  data  command 
            // ID
            endID = this.command.executeCommand("add", [endPoint]);
            // ID
            const item = this.page.graph.findById(startID);
            const model = {
                syncEndID: endID
            };
            //  syncEndID 
            this.page.graph.updateItem(item, model);
        } else if(item.mType === "concurrencyNode") {
            let startPoint = {};
            let endPoint = {};
            let startID, endID;
            //  
            //  item  data   
            startPoint = deepClone(mItem);
            //  data X/Y
            startPoint.offsetX = this.offsetX;
            startPoint.offsetY = this.offsetY;
            //  X/Y 
            const xy = graph.getPointByClient(e.x, e.y);
            startPoint.x = xy.x;
            startPoint.y = xy.y;
            //  data  size 
            startPoint.size = [];
            startPoint.size = mItem.size.split("*");
            //  data  type  node
            startPoint.type = "node";
            //  data  label
            startPoint.name = "";
            startPoint.label = "";
            //  endPoint   
            endPoint = deepClone(startPoint);
            // 
            startPoint.nodeKind =  0;
            //  
            // TODO:  nodeList 
            startPoint.attr = {
                bindForm: '',
                time: 0,
                inChargeGroupID: '',
                inChargeUserID: '',
                roomGroups: [],
                needDevice: false,
                needAuditing: false
            };
            // TODO:  nodeList 
            startPoint.attrName = [
                {
                    name: '',
                    attribute: 'bindForm',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '',
                    attribute: 'inChargeGroupID',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '',
                    attribute: 'inChargeUserID',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '',
                    attribute: 'roomGroups',
                    type: 'multiSelect',
                    clientType: 'sell'
                },
                {
                    name: '',
                    attribute: 'time',
                    type: 'inputNumber',
                    min: 0,
                    clientType: 'sell'
                },
                {
                    name: '',
                    attribute: 'needDevice',
                    type: 'singleCheck',
                    required: true,
                    clientType: 'sell'
                },
                {
                    name: '',
                    attribute: 'needAuditing',
                    type: 'singleCheck',
                    required: true,
                    clientType: 'sell'
                }
            ];
            //  data  command 
            // ID
            startID = this.command.executeCommand("add", [startPoint]);
            //  
            endPoint.offsetY += 100;
            endPoint.y += 100;
            //  endPoint  label
            endPoint.name = "";
            endPoint.label = "";
            //  endPoint  ID
            endPoint.concurrencyBeginID = startID;
            // 
            endPoint.nodeKind =  6;
            //  data  command 
            // ID
            endID = this.command.executeCommand("add", [endPoint]);
            // ID
            const item = this.page.graph.findById(startID);
            const model = {
                concurrencyEndID: endID
            };
            //  syncEndID 
            this.page.graph.updateItem(item, model);
        }else {
            //  item  data   
            let data = deepClone(item);
            //  data X/Y
            data.offsetX = this.offsetX;
            data.offsetY = this.offsetY;
            //  X/Y 
            const xy = graph.getPointByClient(e.x, e.y);
            data.x = xy.x;
            data.y = xy.y;
            //  data  size 
            data.size = item.size.split("*");
            //  data  type  node
            data.type = "node";
            //  data  command 
            this.command.executeCommand("add", [data]);
        }
      }
    },
    /**
     * event
     */
    bindEvent() {
      //  eventBus  afterAddPage 
      eventBus.$on("afterAddPage", page => {
        // page
        this.page = page;
        // command
        this.command = page.command;
      });
    }
  }
};
</script>

<style scoped>
.itempannel {
  height: 100%;
  position: absolute;
  left: 0px;
  z-index: 2;
  background: #f7f9fb;
  width: 200px;
  padding-top: 8px;
  border-right: 1px solid #e6e9ed;
}
.itempannel ul {
  padding: 0px;
  padding-left: 16px;
}
.itempannel li {
  color: rgba(0, 0, 0, 0.65);
  border-radius: 4px;
  width: 160px;
  height: 28px;
  line-height: 26px;
  padding-left: 8px;
  border: 1px solid rgba(0, 0, 0, 0);
  list-style-type: none;
}
.itempannel li:hover {
  background: white;
  border: 1px solid #ced4d9;
  cursor: move;
}

.itempannel .pannel-type-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px;
}
</style>
