import G6 from "@antv/g6/build/g6";
import { uniqueId } from '../../utils'
// G6
import Shape from '@antv/g/src/shapes'
const customNode = {
  init() {
    // G6 customNode
    G6.registerNode("customNode", {
      /**
       * 
       * @param  {Object} cfg 
       * @param  {G.Group} group 
       * @return {G.Shape}  node.get('keyShape') 
       */
      draw(cfg, group) {
        // size
        let size = cfg.size;
        // size170*34
        if(!size){
          size=[170,34]
        }
        // NUMBER bbox
        const width = parseInt(size[0]);
        const height = parseInt(size[1]);
        // color
        const color = cfg.color;
        //  drag-node
        const offsetX = -width / 2
        const offsetY = -height / 2
        const mainId = 'rect' + uniqueId()
        // 
        const shape = group.addShape("rect", {
          attrs: {
            // rectID
            id: mainId,
            // XY
            x: offsetX,
            y: offsetY,
            // 
            width: width,
            height: height,
            // 
            stroke: "#ced4d9",
            // 
            fill: '#fff',//fill 
            // 
            radius: 4,
          }
        });
        // 
        group.addShape("rect", {
          attrs: {
            x: offsetX,
            y: offsetY,
            // 4
            width: 4,
            height: height,
            // 
            fill: color,
            // 
            parent: mainId,
            //  
            radius: [4, 0, 0, 4]
          }
        });
        // 
        group.addShape("rect", {
          zIndex: -99,
          attrs: {
            x: offsetX,
            y: offsetY,
            width: width,
            height: height,
            // 
            fill: '#ced4d9',
            // 
            radius: 8,
            opacity: 0.3,
            isAnimate: true
          }
        });
        //  image
        group.addShape("image", {
          attrs: {
            x: offsetX + 16,
            y: offsetY + 8,
            width: 16,
            height: 16,
            img: cfg.image,
            parent: mainId
          }
        });
        //  stateImage
        group.addShape("image", {
          attrs: {
            x: offsetX + width - 32,
            y: offsetY + 8,
            width: 16,
            height: 16,
            parent: mainId,
            img: cfg.stateImage
          }
        });
        // backImage
        if(cfg.backImage){
          // 
          const clip = new Shape.Rect({
            attrs: {
              x: offsetX,
              y: offsetY,
              width: width,
              height: height,
              fill:'#fff',
              radius: 4
            }
        });
          // clip
          group.addShape("image", {
            attrs: {
              x: offsetX,
              y: offsetY,
              width: width,
              height: height,
              img: cfg.backImage,
              clip: clip
            }
          });
        }
        // labellabel
        if (cfg.label) {
           group.addShape("text", {
            attrs: {
              id: 'label' + uniqueId(),
              x: offsetX + width / 2,
              y: offsetY + height / 2,
              textAlign: "center",
              textBaseline: "middle",
              text: cfg.label,
              parent: mainId,
              fill: "#565758"
            }
          });
        }
        // inPointsanchor 
        if (cfg.inPoints) {
          for (let i = 0; i < cfg.inPoints.length; i++) {
            let x,
              y = 0;
            //0 1
            if (cfg.inPoints[i][0] === 0) {
              y = 0;
            } else {
              y = height;
            }
            x = width * cfg.inPoints[i][1];
            const id = 'circle' + uniqueId();
            // anchor
            // anchor
            group.addShape("circle", {
              attrs: {
                id: 'circle' + uniqueId(),
                parent: id,
                x: x + offsetX,
                y: y + offsetY,
                // 10
                r: 10,
                // boolean
                isInPointOut: true,
                // 
                fill: "#1890ff",
                opacity: 0
              }
            });
            // anchoranchor
            group.addShape("circle", {
              attrs: {
                id: id,
                x: x + offsetX,
                y: y + offsetY,
                // 3
                r: 3,
                // boolean
                isInPoint: true,
                // 
                fill: "#fff",
                // 
                stroke: "#1890ff",
                // 
                opacity: 0
              }
            });
          }
        }
        // outPointsanchor 
        if (cfg.outPoints) {
          for (let i = 0; i < cfg.outPoints.length; i++) {
            let x,
              y = 0;
            //0 1
            if (cfg.outPoints[i][0] === 0) {
              y = 0;
            } else {
              y = height;
            }
            x = width * cfg.outPoints[i][1];
            const id = 'circle' + uniqueId()
            // anchor
            group.addShape("circle", {
              attrs: {
                id: 'circle' + uniqueId(),
                parent: id,
                x: x + offsetX,
                y: y + offsetY,
                r: 10,
                // // boolean
                isOutPointOut: true,
                fill: "#1890ff",
                opacity: 0//0 0.3
              }
            });
            group.addShape("circle", {
              attrs: {
                id: id,
                x: x + offsetX,
                y: y + offsetY,
                r: 3,
                // // boolean
                isOutPoint: true,
                fill: "#fff",
                stroke: "#1890ff",
                opacity: 0
              }
            });
          }
        }
        group.sort()
        // 
        return shape;
      },
      /**
       *  draw 
       *  keyShape  selectedactive 
       * @param  {String} name 
       * @param  {Object} value 
       * @param  {Node} item 
       */
      setState(name, value, item) {
        // 
        const group = item.getContainer();
        // group.sort()  children[0] rect children[1]  keyShape
        const shape = group.get("children")[1]; //  draw 

        const children = group.findAll(g => {
          return g._attrs.parent === shape._attrs.id
        });
        const circles = group.findAll(circle => {
          return circle._attrs.isInPoint || circle._attrs.isOutPoint;
        });
        // style
        const selectStyles = () => {
          shape.attr("fill", "#f3f9ff");
          shape.attr("stroke", "#6ab7ff");
          shape.attr("cursor", "move");
          children.forEach(child => {
            child.attr("cursor", "move");
          });
          circles.forEach(circle => {
            circle.attr('opacity', 1)
          })
        };
        // style
        const unSelectStyles = () => {
          shape.attr("fill", "#fff");
          shape.attr("stroke", "#ced4d9");
          circles.forEach(circle => {
            circle.attr('opacity', 0)
          })
        };
        const addEdgeHoverStyles = () => {
            shape.attr("fill", "#f3f9ff");
            shape.attr("stroke", "#6ab7ff");
            circles.forEach(circle => {
                circle.attr('opacity', 1)
            })
        };
        // style
        const addErrorStyles = () => {
          shape.attr("fill", "#fff");
          shape.attr("stroke", "#FF0000");
          circles.forEach(circle => {
            circle.attr("opacity", 1);
          });
        };
        switch (name) {
          // value
          case "selected":
          case "hover":
            if (value) {
              selectStyles()
            } else {
              unSelectStyles()
            }
            break;
          case "addEdgeHover":
            if(value) {
                addEdgeHoverStyles();
            } else {
                unSelectStyles();
            }
            break;
          case "running":
            if(value) {
              //  group  isAnimate  shape
              group.find( item => {
                if(item._attrs.isAnimate) {
                  // 
                  item.animate({
                    x: item._attrs.x - 10,
                    y: item._attrs.y - 10,
                    width : item._attrs.width + 20,
                    height: item._attrs.height + 20,
                    repeat: true
                  }, 1000, 'easeLinear');
                }
              })
            } else {
              group.find( item => {
                if(item._attrs.isAnimate) {
                  item.stopAnimate();
                  item.attr('width', item._attrs.width - 20);
                  item.attr('height', item._attrs.height - 20);
                  item.attr('x', item._attrs.x + 10);
                  item.attr('y', item._attrs.y + 10);
                }
              })

            }
            break;
          case "error":
            if (value) {
              addErrorStyles();
            } else {
              unSelectStyles();
            }
            break;
        }
      }
    });
  }
}

export default customNode
