import context from "../../../../src/main"
import eventBus from "../utils/eventBus";
import { uniqueId } from '../utils'
let startItem = null;
export default {
    getEvents() {
        return {
            'node:click': 'onMouseClick'
        };
    },
    /**
     * 
     * @param e event
     */
    onMouseClick(e){
        const graph = self.graph;
        const item = e.item;
        //  startItem  null
        // 
        if(!startItem) {
            startItem = item;
            graph.setItemState(startItem, 'addEdgeHover', true);
            //  paint
            graph.paint();
        }else{
            //   
            if(this.checkCondition(item, graph)) {
                // 
                //  BeginID 
                if(startItem._cfg.model.syncEndID) {
                    // 
                    let node = graph.findById(item._cfg.id);
                    //  model syncBeginID
                    let model = {
                        syncBeginID: startItem._cfg.id
                    };
                    // 
                    graph.update(node, model);
                    // 
                    // 
                }else if (startItem._cfg.model.syncBeginID && startItem._cfg.model.mType !== "syncNode") {
                    let node = this.graph.findById(item._cfg.id);
                    let model = {
                        syncBeginID: startItem._cfg.model.syncBeginID
                    };
                    graph.update(node, model);
                    // 
                    // 
                }else if(startItem._cfg.model.concurrencyEndID) {
                    let node = graph.findById(item._cfg.id);
                    let model = {
                        concurrencyBeginID: startItem._cfg.id
                    };
                    graph.update(node, model);
                    // 
                    // 
                }else if(startItem._cfg.model.concurrencyBeginID && startItem._cfg.model.mType !== "concurrencyNode") {
                    let node = this.graph.findById(item._cfg.id);
                    let model = {
                        concurrencyBeginID: startItem._cfg.model.concurrencyBeginID
                    };
                    graph.update(node, model);
                }
                // 
                if(startItem._cfg.model.mType === "cycleNode") {
                    // 
                    let node = this.graph.findById(startItem._cfg.id);
                    //  model  loopBeginNodeID 
                    let model = {
                        loopBeginNodeID: item._cfg.id
                    };
                    // 
                    graph.update(node, model);
                }
                //  
                // 
                const model = {
                    id: 'edge' + uniqueId(),
                    //  startItem
                    source: startItem,
                    // item
                    target: item,
                    sourceId: startItem._cfg.id,
                    targetId: item._cfg.id,
                    // start: {x: 0, y: 17},
                    // end: {x: 0, y: 17},
                    shape: 'customEdge',
                    type: 'edge'
                };
                if(startItem._cfg.model.mType === "cycleNode") {
                    //  
                    model.start = {x: 0, y: -17};
                } else if(item._cfg.model.mType === "cycleNode") {
                    //  
                    model.end = {x: 0, y: 17};
                }
                //  eventBus  addItem  
                eventBus.$emit('addItem', model);
                graph.setItemState(startItem, 'addEdgeHover', false);
                //  
                startItem = null;
                // 
                this.graph.paint()
            } else {
                // 
                if(startItem) {
                    //  warn true  warning 
                    const model = {
                        id: 'edge' + uniqueId(),
                        //  startItem
                        source: startItem,
                        // item
                        target: item,
                        sourceId: startItem._cfg.id,
                        targetId: item._cfg.id,
                        // start: {x: 0, y: 17},
                        // end: {x: 0, y: 17},
                        shape: 'customEdge',
                        type: 'edge',
                        warn: true
                    };
                    if(startItem._cfg.model.mType === "cycleNode") {
                        //  
                        model.start = {x: 0, y: -17};
                    } else if(item._cfg.model.mType === "cycleNode") {
                        //  
                        model.end = {x: 0, y: 17};
                    }
                    //  eventBus  addItem  
                    eventBus.$emit('addItem', model);
                    graph.setItemState(startItem, 'addEdgeHover', false);
                    //  
                    startItem = null;
                    // 
                    this.graph.paint()
                }
            }
        }

    },
    /**
     * 
     *  
     * @param item 
     * @param graph
     */
    checkCondition(item, graph) {
        //  
        if(startItem._cfg.id === item._cfg.id){
            // 
            context.$notify.error({
                title: '',
                message: ''
            });
            graph.setItemState(startItem, 'addEdgeHover', false);
            //  
            startItem = null;
            return false;
        }
        //  
        if(this.graph.find("edge", edge => {
            return (edge.getSource()._cfg.id === startItem._cfg.id) && (edge.getTarget()._cfg.id === item._cfg.id);
        })) {
            // 
            context.$notify.error({
                title: '',
                message: ''
            });
            graph.setItemState(startItem, 'addEdgeHover', false);
            //  
            startItem = null;
            return false;
        }
        //  
        if(item._cfg.model.mType === "syncNode" && item._cfg.model.syncBeginID) {
            //  syncBeginID 
            if(startItem._cfg.model.syncBeginID) {
                //  syncBeginID 
                if(startItem._cfg.model.syncBeginID !== item._cfg.model.syncBeginID) {
                    // 
                    context.$notify.warning({
                        title: '',
                        message: ''
                    });
                    return false;
                }
            } else {
                // 
                context.$notify.warning({
                    title: '',
                    message: ''
                });
                return false;
            }
        }
        //  
        if(item._cfg.model.mType === "concurrencyNode" && item._cfg.model.concurrencyBeginID) {
            //  concurrencyBeginID 
            if(startItem._cfg.model.concurrencyBeginID) {
                //  concurrencyBeginID 
                if(startItem._cfg.model.concurrencyBeginID !== item._cfg.model.concurrencyBeginID) {
                    // 
                    context.$notify.warning({
                        title: '',
                        message: ''
                    });
                    return false;
                }
            } else {
                // 
                context.$notify.warning({
                    title: '',
                    message: ''
                });
                return false;
            }
        }
        //  
        if(item._cfg.model.mType === "wfdNode" && item._cfg.model.syncBeginID) {
            //  syncBeginID 
            if(startItem._cfg.model.syncBeginID) {
                //  syncBeginID 
                if(startItem._cfg.model.syncBeginID !== item._cfg.model.syncBeginID) {
                    // 
                    context.$notify.warning({
                        title: '',
                        message: ''
                    });
                    return false;
                }
            } else {
                // 
                context.$notify.warning({
                    title: '',
                    message: ''
                });
                return false;
            }
        }
        //  
        if(item._cfg.model.mType === "wfdNode" && item._cfg.model.concurrencyBeginID) {
            //  concurrencyBeginID 
            if(startItem._cfg.model.concurrencyBeginID) {
                //  concurrencyBeginID 
                if(startItem._cfg.model.concurrencyBeginID !== item._cfg.model.concurrencyBeginID) {
                    // 
                    context.$notify.warning({
                        title: '',
                        message: ''
                    });
                    return false;
                }
            } else {
                // 
                context.$notify.warning({
                    title: '',
                    message: ''
                });
                return false;
            }
        }
        //  
        if(startItem._cfg.model.mType === "cycleNode" && item._cfg.model.mType === "endNode"){
            // 
            context.$notify.error({
                title: '',
                message: ''
            });
            graph.setItemState(startItem, 'addEdgeHover', false);
            //  
            startItem = null;
            return false;
        }
        return true;
    }
}
