<template>
  <div class="page">
    <div ref="mountNode" :id="pageId" class="graph-container" style="position: relative;"></div>
  </div>
</template>


<script>
import G6 from "@antv/g6/build/g6";
import { initBehavors } from "../../behavior";
export default {
  data() {
    return {
      resizeFunc: ()=>{},
      // g6divid
      pageId: "graph-container",
      // g6 graph
      graph: null,
    };
  },
  props: {
    // height
    height: {
      type: Number,
      default: 0
    },
    // width
    width: {
      type: Number,
      default: 0
    },
    // data
    data: {
      type: Object,
      default: () => {}
    }
  },
  created() {
    // G6 Behavors
    // behaviorindexexportfunction
    initBehavors();
  },
  mounted() {
    // Vue.nextTick
    this.$nextTick(() => {
      this.init();
    });
  },
  methods: {
    init() {
      // g6heightwidth
      const height =  this.height - 145
      const width =  this.width - 575
      // Graph
      this.graph = new G6.Graph({
        container: "graph-container",
        height: height,
        width: width,
        modes: {
          // default mode behavior
          default: [
            "drag-canvas",
            "zoom-canvas",
            "hover-node",
            "select-node",
            "hover-edge",
            "keyboard",
            "customer-events",
            "add-menu",
            "canvas-click"
          ],
          //  mode  behavior
          mulitSelect: ["mulit-select"],
          //  edge  behavior
          addEdge: ["add-edge", "drag-canvas", "zoom-canvas", "hover-edge", "keyboard"],
          //  behavior
          moveNode:["drag-item"]
        }
      });
      //  this.$parent dataeditorcommand
      const { editor, command } = this.$parent;
      // editorafterAddPage   graph  command
      editor.emit("afterAddPage", { graph: this.graph, command });
      this.initResizeEvent();
      //  readData 
      this.readData();
    },
    /**
     *  data  g6 
     */
    readData() {
      let data = this.data;
      this.graphData = this.data;
      let startID, endID, cycleID;
      if (data) {
        for(let i in data.nodes){
          //
          switch (data.nodes[i].mType) {
            case 'startNode':
              startID = data.nodes[i].id;
              break;
            case 'endNode':
              endID = data.nodes[i].id;
              break;
            case 'cycleNode':
              cycleID = data.nodes[i].id;
              break;
          }
        }
        this.graph.read(data);
      }
    },
    /**
     * resizecanvasevent
     */
    initResizeEvent() {
      const page = this.$refs['mountNode'];
      const graph = this.graph;
      this.resizeFunc = ()=>{
        graph.changeSize(page.offsetWidth, window.innerHeight - 90);
      };
      window.addEventListener("resize", this.resizeFunc);
    }
  }
};
</script>

<style scoped>
.page{
  margin-left:200px;
  margin-right: 200px;
}
</style>
