import { uniqueId } from '../../utils';
import eventBus from "../../utils/eventBus";

/**
 * @param id EditorID
 * @param graph Editor g6 graph 
 */
export default class Editor {
  /**
   * 
   */
  constructor() {
    //  id
    this.id = uniqueId();
  }
  getGrpah() {
    return this.graph
  }

  /**
   *
   * @param event 
   * @param params 
   */
  emit(event, params) {
    if (event === 'afterAddPage') {
      //  event  afterAddPage  Editor  graph 
      this.graph = params.graph
    }
    //  eventBus  ItemPanel  afterAddPage  params
    eventBus.$emit(event, params)
  }
  on(event) {
    switch (event) {
      case 'changeNodeData':
        this.graph.refresh()
        break
    }
  }

  /**
   * graph 
   * @param type  type
   * @param item 
   */
  add(type, item) {
    this.graph.add(type, item)
  }

  /**
   * graph 
   * @param item  ID 
   * @param model antvg6 API
   */
  update(item, model) {
    this.graph.update(item, model)
  }

  /**
   * graph 
   * @param item  ID 
   */
  remove(item) {
    const node = this.graph.findById(item.id)
    this.graph.remove(node)
  }
}
