import record from "../../../../../static/record.png"
export default (_self, h) => {
    // Array
    return [
        h(
            "el-image",
            {
                props: {
                    src: record
                }
            }
        )
    ]
};


export let recordConf = {
    // 
    type: 'record',
    // 
    config: true,
    value: '',
    attr: {
        label: '',
        require: true,
    },
    attrName: [
        {
            name: '',
            attribute: 'label',
            type: 'input',
            clientType: 'sell'
        },
        {
            name: '',
            attribute: 'require',
            type: 'singleCheck',
            clientType: 'sell'
        },
    ]

}
