export default (_self, h) => {
    // Array
    return [
        h(
            // 
            "el-upload",
            // 
            {
                // prop
                props: {
                    value: _self.obj.value || ""
                },
                //  HTML 
                attrs: {
                    listType: "picture-card",
                    action: "#",
                    autoUpload: false
                },
                //  on 
                on: {

                }
            },
            //  (VNodes) `createElement()` 
            [h("i",{
                class: {
                    'el-icon-plus': true
                }
            })]
        )
    ];
};


export let videoConf = {
    // 
    type: 'video',
    // 
    config: true,
    value: '',
    attr: {
        label: '',
        require: true,
    },
    attrName: [
        {
            name: '',
            attribute: 'label',
            type: 'input',
            clientType: 'sell'
        },
        {
            name: '',
            attribute: 'require',
            type: 'singleCheck',
            clientType: 'sell'
        },
    ]

}
